"use client";
import { useRef, useEffect } from "react";
import { motion } from "framer-motion";

export const Cover = ({ children }: { children: React.ReactNode }) => {
  const divRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!divRef.current) return;

    const updateMousePosition = (e: MouseEvent) => {
      if (!divRef.current) return;
      const rect = divRef.current.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;
      divRef.current.style.setProperty("--mouse-x", `${x}px`);
      divRef.current.style.setProperty("--mouse-y", `${y}px`);
    };

    divRef.current.addEventListener("mousemove", updateMousePosition);

    return () => {
      // eslint-disable-next-line react-hooks/exhaustive-deps
      divRef.current?.removeEventListener("mousemove", updateMousePosition);
    };
  }, []);

  return (
    <div
      ref={divRef}
      className="relative inline-block group"
      style={{
        // @ts-ignore
        "--mouse-x": "50%",
        "--mouse-y": "50%",
      }}
    >
      <motion.div
        className="absolute inset-0 opacity-0 group-hover:opacity-100 transition-opacity duration-500"
        style={{
          background:
            "radial-gradient(circle 100px at var(--mouse-x) var(--mouse-y), rgba(16, 185, 129, 0.2), transparent 80%)",
        }}
      />
      <span className="relative z-10 bg-clip-text text-transparent bg-gradient-to-b from-emerald-400 via-emerald-500 to-emerald-600">
        {children}
      </span>
    </div>
  );
};
